$(".process-item").mouseover(function () {
    if (window.innerWidth > 992){
        $(".process-item").removeClass("_active")
        $(this).addClass("_active")
    }
})

$(".process-item__arrow").click(function () {
    if (window.innerWidth <= 992){
        $(".process-item").removeClass("_active")
        $(this).parents(".process-item").addClass("_active")
    }
})
